<template>
	<div class="content">
		<nav-bar bgColor="transparent" arrow="gray"><a href="javascript:"></a></nav-bar>
		<h1 @click="inputFile" v-if="!fileList.length"></h1>
		<van-uploader v-model="fileList" multiple id="uploadfiles" v-show="fileList.length" :max-count="1"
			:after-read="afterRead" />
		<div class="login-box">
			<p class="lsolid"></p>
			<div class="login">
				<router-link to="/login">登录</router-link>
				<span></span>
				<router-link to="/reg">注册</router-link>
			</div>
			<p class="rsolid"></p>
		</div>
		<ul>
			<field label="用户名" v-model="username" class='lifirst'></field>
			<field label="密码" :types="'password'" v-model="password"></field>
		</ul>
		<div class="footbox">
			{{msg}}
			<btn :texts="'注册'" @click="reg" backgroundColor='#4c4f50'></btn>
			<a href="javascript:;" class="tishi">忘记密码？</a>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue';
	import {
		Uploader
	} from 'vant';
	Vue.use(Uploader);
	export default {
		name: 'reg',
		data() {
			return {
				username: {
					type: [String, Number],
					default: ''
				},
				password: {
					type: [String, Number],
					default: ''
				},
				msg: '',
				fileList: [],
				files: ''
			}
		},
		methods: {
			inputFile(e) {
				let uploadfile = document.querySelector('#uploadfiles');
				uploadfile.click();
			},
			afterRead(file) {
				this.files = file

			},
			reg() {
				let formData = new FormData(); //创建formData类型
				formData.append("icon", this.files.file); //icon键的值是个文件体
				console.log(this.files.file);
				formData.append("username", this.username)
				formData.append("password", this.password)
				formData.append("nikename", "可口可乐")
				this.$axios({
					url: 'reg',
					method: 'post',
					data: formData
				}).then(res => {
					console.log(res);
					if (res.data.err == 0) {
						this.$router.push('/login')
						console.log(res);
					} else {
						this.msg = res.data.msg
						console.log(res);
					}
				})

			}
		}
	}
</script>

<style scoped>
	body {
		background: #f2f4f5;
	}

	.content {
		max-width: 6.4rem;
