		display: block;
		width: 0.23rem;
		height: 0.25rem;
		background: url(../assets/img/history1.png) no-repeat 0 0;
		background-size: 100%;
		margin-left: 0.3rem;
	}

	.content h1 {
		width: 2.18rem;
		height: 1.35rem;
		background: url(../assets/img/my_cz.png) no-repeat 0 0;
		background-size: 100%;
		margin: 0 auto;
		margin-top: 1.22rem;
	}

	.content .login {
		width: 2.0rem;
		height: 0.38rem;
		margin: 0 auto;
		margin-top: 0.85rem;
	}

	.login a {
		width: 0.97rem;
		height: 0.38rem;
		font-size: 0.35rem;
		line-height: 0.38rem;
		float: left;
		color: #4c4f50;
		text-align: center;
	}

	.login span {
		float: left;
		height: 0.38rem;
		border-left: 1px solid #4c4f50;
	}

	.content .login-box {
		position: relative;
	}

	.content .login-box .lsolid {
		width: 1.96rem;
		height: 0.18rem;
		border-bottom: 1px solid #4c4f50;
		position: absolute;
		top: 0;
		left: 0.1rem;
	}

	.content .login-box .rsolid {
		width: 1.96rem;
		height: 0.18rem;
		border-bottom: 1px solid #4c4f50;
		position: absolute;
		top: 0;
		right: 0.1rem;
	}

	.content ul {
		width: 5.78rem;
		height: 1.92rem;
		border: 1px solid #e5e7e8;
		background: #fff;
		margin: 0 auto;
		margin-top: 0.68rem;
	}

	.content ul li {
		height: 0.95rem;
		position: relative;
	}

	.content ul li.lifirst {
		border-bottom: 1px solid #e5e7e8;
	}

	.content ul li input {
		padding-left: 1.24rem;
		width: 4.54rem;
		height: 100%;
		font-size: 0.4rem;
	}

	.content ul li span {
		width: 1.23rem;
		height: 0.49rem;
		position: absolute;
		top: 0.26rem;
		left: 0;
		border-right: 1px solid #676868;
		color: #676868;
		font-size: 0.25rem;
		line-height: 0.49rem;
		text-align: center;
	}

	.content .footbox {
		width: 4.65rem;
