<template>
	<div class="detail">

		<nav-bar />

		<div class="content" v-if="detail.title">
			<div class="header clear">
				<h2><img :src="detail.author_icon" alt="" /></h2>
				
			</div>
			<div class="cont">
				<p>{{detail.author}}</p>
				<h3>{{detail.title}}</h3>
				<div class="time">
					<p>{{cptTime}} <span><img src="../assets/img/zan.png" alt="" /></span></p>
				</div>
				<div class="text-box">{{detail.content}}</div>
			</div>
		</div>

		<div class="foot-btn">
			<ul>
				<li class="say"><a href="javascript:;">
						<i></i><span>0</span>
					</a></li>
				<li class="zan"><a href="javascript:;">
						<i></i><span>0</span>
					</a></li>
				<li class="xing"><a href="javascript:;">
						<!-- <i><img :src="xing" alt=""/></i> -->
						<i><img :src="xing" alt="" /></i>
					</a></li>
				<li class="fx"><a href="javascript:;">
						<i><img :src="require('../assets/img/fx.png')" alt="" /></i>
					</a></li>
			</ul>
		</div>

	</div>
</template>

<script>
	import xing from '../assets/img/xing.png';


	// let fx = require('../assets/img/fx.png')

	// console.log('detai.vue',fx);

	import NavBar from '../components/nav.vue'
	import {mapState} from 'vuex'
	export default {
		name:'detail',
		data() {
			return {
				xing,
			}
		},
		components: {
			NavBar
		},

		mounted() {

			let collectionName = this.$route.query.collectionName;
			let _id = this.$route.params._id;
			this.$store.dispatch('detail/detail',{collectionName,_id})
			// this.$axios({
			// 	url:`news/${collectionName}/${_id}`
			// }).then(res=>{
			// 	if(!res.data.data.author_icon.includes('http')){
			// 		res.data.data.author_icon = this.$baseUrl + res.data.data.author_icon;
			// 	}
			// 	this.detail = res.data.data
			// })
		},
		computed:{
			cptTime(){
				let d = new Date();
				d.setTime(this.detail.time * 1000)
				let year = d.getFullYear();
				let month = d.getMonth() + 1;
				let day = d.getDate();
				let hour = d.getHours();
				let min = d.getMinutes();
				let se = d.getSeconds();
				let addZero = (x)=>{
					if(x < 10){
						x = '0' + x;
					}
					return x
				}
				return year + '年' +addZero(month) + '月' + addZero(day) + '日' + addZero(hour) + ':'  + addZero(min)+':'  + addZero(se)
			},
			...mapState('detail',['detail'])
		}
	}
</script>

