<template>
	<div>
		<!-- <div id="map"></div> -->
		<div id="container"></div>
	</div>
</template>

<script>
	export default {
		name:"lhrMap",
		mounted() {
			console.log(1);
			var map = new BMapGL.Map('container'); // 创建Map实例
			map.centerAndZoom(new BMapGL.Point(121.498205,31.384404), 15); // 初始化地图,设置中心点坐标和地图级别
			map.enableScrollWheelZoom(true); // 开启鼠标滚轮缩放
			var marker1 = new BMapGL.Marker(new BMapGL.Point(121.498205,31.384404));
			// 在地图上添加点标记
			map.addOverlay(marker1);
		}
	}
</script>

<style scoped>
	#container {

		overflow: hidden;
		width: 100%;
		height: 500px;
		margin: 0;
		font-family: "微软雅黑";

	}
</style>
